.wrapper{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    height: 100%;
}
.List_view_wrapper{
    flex-grow: 1;
    padding:16px 8px;
    list-style: none;
	overflow: overlay;
}
.ViewItem{
    background-color: var(--background-sec);
    padding: 8px 16px;
    cursor: pointer;
    transition: background 250ms linear , color 250ms linear;
    border-radius: 6px;
    &:not(:last-child){
        margin-bottom: 3px;
        border-bottom: 1px solid rgb(0 0 0 / 12%);
    }
    &:hover{
    background-color: var(--border-color);
               color: var(--white);
    }
}
.clear-wrapper{
    padding: 8px 16px;
}
.clear-htr-btn{
    display: block;
    width: 100%;
    padding: 6px 12px;
    text-align: center;
    font-size: 1rem;
    background-color: transparent;
    color: var(--BrightRed);
    border-color: transparent;
    border-radius: 6px;
    cursor: pointer;
			border: 2px solid var(--border-color);
            &:is(:hover,:active) {
				border-color: red;
			}
}